import * as React from "react";
import { GastbyImage, getImage } from "gatsby-plugin-image";

const HeroImage = ({
  image,
  alt,
  credit_content,
}: {
  image: string;
  alt: string;
  credit_content: string;
}): React.ReactNode => {
  return (
    <React.Fragment>
      <GastbyImage image={getImage(image)} alt={alt} />
      <p>
        Photo Credit:{" "}
        <div dangerouslySetInnerHtml={{ __html: credit_content }} />
      </p>
    </React.Fragment>
  );
};

export default HeroImage;
